@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.playing-card-title-content{
    background-color: #333;
    box-sizing: border-box;
    color: #F3F3F3; 
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    height: 110px;
    padding: 30px;
    position: relative;
}

.playing-card-title-content-image-container {
    align-items: center;
    background-color: #F3F3F3;
    border-radius: 50%;
    box-shadow: 0 0 10px #666;
    cursor: pointer;
    display: flex;
    height: 50px;
    justify-content: center;
    left: 30px;
    position: absolute;
    top: 80px;
    width: 50px;
}
.playing-card-title-content-image{
    -webkit-transition: width 0.15s linear;
    transition: width 0.15s linear;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    width: 25px;
}
.playing-card-title-content-image-container:hover .playing-card-title-content-image {
    -webkit-animation-duration: 1.2s;
            animation-duration: 1.2s;
    -webkit-animation-delay: 0.15s;
            animation-delay: 0.15s;
    -webkit-animation-name: rotate360;
            animation-name: rotate360;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    width: 30px;
}
@-webkit-keyframes rotate360 {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes rotate360 {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.playing-card-title-content-value {
    font-size: 30px;
    font-weight: 300;
}

.playing-card-title-content-title {
    font-size: 18px;
    text-transform: uppercase;
}
.playing-card-content {
    align-items: center;
    display: flex;
    height: 260px;
    justify-content: center;
}
.playing-card-content img {
    width: 150px;
}

.playing-card-footer{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.playing-card-front {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius:20px;
    height: 100%;
    overflow: hidden;
    position: absolute;
    width: 100%;
}
.playing-card-back{
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background-color: white;
    height:100%;
    position:absolute;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    width: 100%;
}
.playing-card-back-container {
    cursor: pointer;    
    position: relative;
}
.playing-card-back-background {
    width: 380px;
    height: 505px;
    position: absolute;
    top: -10px;
    left: -10px;    
}
.playing-card {
        /*Temporal*/
        margin: 30px auto;
        align-items: center;
        display: flex;
        height: 486px;
        justify-content: center;;
        overflow: hidden;
        -webkit-perspective: 1000px;
                perspective: 1000px;
        width: 366px;
}
.playing-card-container {
    box-sizing: border-box;
    border-radius: 20px;
    box-shadow: 0 0 3px #666;
    height: calc(100% - 6px);
    position: relative;
    -webkit-transition: -webkit-transform 0.8s;
    transition: -webkit-transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    width: calc(100% - 6px);
}

.playing-card-container.flip-card {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
